.chat {
    flex: 0.65;
    background-color: white;
    display: flex;
    flex-direction: column;
}

.chat_header {
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat_headerInfo {
    flex: 1;
    padding-left: 20px;
}

.chat_headerInfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat_headerInfo > p {
    color: gray;
}

.chat_message {
    position: relative;
    font-size: 16px;
    padding: 10px;
    background-color: white;
    margin-bottom: 30px;
}

.chat_timestamp {
    margin-left: 600px;
    font-size: small;
}

.chat_name {
    position: absolute;
    top: -25px;
    font-weight: 800;
    font-size: small;
    padding-left: 20px;
}

.chat_headerRight {
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat_details {
    flex-direction: row;
    padding: 15px;
}

.chat_extra {
    flex: 1;
    flex-direction: column;
    padding: 10px;
    margin-top: 20px;
}

.chat_body {
    background-color: lightgray;
    background-position: center;
    padding: 30px;
    overflow: scroll;
    flex: 1;
}

.chat_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightblue;
}

.chat_footer > form {
    flex: 1;
    display: flex;
}

.chat_footer > form > input {
    flex: 1;
    border-radius: 30px;
    padding: 10px;
}